.popularProducts {
  padding: 15px 0 45px;
  position: relative;
}

.popularProducts .title {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 30px;
  text-align: center;
  padding: 25px 0 40px;
}

.popularProducts .obl-carousel {
  position: relative;
  width: 100%;
}

.popularProducts .obl-carousel .stage {
  overflow: hidden;
  width: 100%;
}

.popularProducts .holste {
  width: 9999%;
}

.popularProducts .card {
  margin: 0 20px;
  float: left;
  position: relative;
  width: 245px;
}

.popularProducts .card.new:before {
  position: absolute;
  top: 0;
  right: 0;
  content: "NEW";
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  color: #fff;
  font-weight: 300;
  background-color: #00838f;
  width: 40px;
  padding: 4px 0;
  text-align: center;
  top: 10px;
}

.popularProducts .card .dropPrice {
  position: absolute;
  top: 0;
  right: 0;
  content: "NEW";
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  color: #fff;
  font-weight: 300;
  background-color: #b01b52;
  width: 40px;
  padding: 4px 0;
  text-align: center;
  top: 10px;
}

.popularProducts .card .infoProduct {
  float: left;
  width: 120px;
}

.popularProducts .card .infoProduct .nameProduct {
  font-family: 'Roboto' , sans-serif;
  font-size: 16px;
  color: #333333;
}

.popularProducts .card .infoProduct .price {
  font-family: 'Roboto' , sans-serif;
  color: #000;
  font-size: 18px;
  margin-top: 10px;
  font-weight: 300;
}

.popularProducts .card img {
  margin-bottom: 20px;
}

.popularProducts .card .addToCart {
  color: #c3567e;
  font-size: 14px;
  background-color: #f8f8f8;
  width: 110px;
  height: 30px;
  float: right;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: 'Roboto' , sans-serif;
  cursor: pointer;
}

.popularProducts .card .breakPrice {
  color: #000;
  font-size: 14px;
  font-family: 'Roboto' , sans-serif;
  font-weight: 300;
  display: inline-block;
  position: relative;
  margin-top: 10px;
}

.popularProducts .card .breakPrice:before {
  background-color: #b01b52;
  width: 100%;
  height: 1px;
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
}

.popularProducts .owl-nav {
  position: absolute;
  top: 150px;
  width: 100%;
}

.popularProducts .owl-prev, .popularProducts .owl-next {
  position: absolute;
  height: 35px;
  width: 20px;
  background-repeat: no-repeat;
  background-size: container;
  cursor: pointer;
}

.popularProducts .owl-prev {
  left: -20px;
  background-image: url("../images/icons/popularLeft.png");
}

.popularProducts .owl-next {
  right: -30px;
  background-image: url("../images/icons/popularRight.png");
}

.youWasSee {
  padding: 0px 0 50px;
}

.youWasSee .container {
  border-top: 1px solid #e9e9e9;
  padding-top: 30px;
}

.youWasSee .title {
  font-family: 'Source Sans Pro' , sans-serif;
  font-size: 30px;
  color: #000;
  text-align: center;
  text-transform: uppercase;
  padding-bottom: 25px;
}

.youWasSee .owl-carousel {
  position: relative;
}

.youWasSee .owl-nav {
  position: absolute;
  top: 50%;
  margin-top: -17px;
  width: 100%;
}

.youWasSee .owl-prev, .youWasSee .owl-next {
  position: absolute;
  height: 35px;
  width: 20px;
  background-repeat: no-repeat;
  background-size: container;
}

.youWasSee .owl-prev {
  left: -30px;
  background-image: url("../images/icons/popularLeft.png");
}

.youWasSee .owl-next {
  right: -30px;
  background-image: url("../images/icons/popularRight.png");
}

.content {
  margin-top: 40px;
}

.content .priceRow {
  width: 100%;
