}

.content .orderKeys .addToCart {
  padding: 0 25px;
  height: 50px;
  text-transform: uppercase;
  margin-right: 10px;
  background-color: #b01b52;
  font-family: 'Roboto' , sans-serif;
  font-size: 18px;
  color: #fff;
  font-weight: 300;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  cursor: pointer;
  transition-duration: .5s;
}

.content .orderKeys .addToCart:hover {
  opacity: .9;
}

.content .orderKeys .orderOneClick {
  padding: 0 25px;
  height: 50px;
  text-transform: uppercase;
  background-color: #f8f8f8;
  font-family: 'Roboto' , sans-serif;
  font-size: 18px;
  color: #b01b52;
  font-weight: 300;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  cursor: pointer;
  transition-duration: .5s;
}

.content .orderKeys .orderOneClick:hover {
  opacity: .9;
}

.content .orderKeys .none {
  font-family: 'Roboto' , sans-serif;
  font-size: 18px;
  color: #000;
  font-weight: 300;
  text-transform: uppercase;
  float: left;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 25px;
}

.content .orderKeys .share {
  width: 50px;
  float: right;
  height: 50px;
  background-image: url("../images/icons/shareRed.png");
  background-repeat: no-repeat;
  background-position: center;
  text-transform: uppercase;
  background-color: #f8f8f8;
  font-family: 'Roboto' , sans-serif;
  font-size: 18px;
  font-weight: 300;
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  cursor: pointer;
  transition-duration: .5s;
  position: relative;
}

.content .orderKeys .share:hover {
  background-image: url("../images/icons/share.png");
  background-color: #b01b52;
}

.content .orderKeys .share .shareList {
  background-color: #fff;
  border: 1px solid #e9e9e9;
  padding: 10px;
  position: absolute;
  bottom: 100%;
  display: none;
}

.content .orderKeys .share .shareList .title {
  margin-bottom: 5px;
  font-family: 'Source Sans Pro' ,sans-serif;
  font-size: 26px;
  color: #000;
  font-weight: 400;
}

.content .orderKeys .share .shareList .shareLink {
  padding-bottom: 10px;
}

.content .orderKeys .share:hover {
  opacity: .9;
}

.content .col.size {
  display: inline-block;
}

.content .helpSize {
  color: #b01b52;
  font-family: 'Roboto' , sans-serif;
  font-weight: 300;
  font-size: 14px;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
}

.content .helpSize:hover {
  text-decoration: underline;
  text-decoration-style: dotted;
}

.content .listTop li, .content .anyList li {
  padding-bottom: 15px;
}

.content .listTop li b, .content .anyList li b {
  width: 105px;
  font-family: "Roboto", sans-serif;
  font-family-font-size: 14px;
  font-family-color: #000;
  font-family-font-weight: 700;
  font-family-float: left;
  font-family-display: block;
}

.content .anyList li span {
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000;
}

.content .articule {
  font-family: "Roboto", sans-serif;
  font-family-font-size: 14px;
  font-family-font-weight: 400;
  font-family-color: #000;
}

.content .customSelect {
  display: inline-block;
  color: #b01b52;
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  font-weight: 300;
  background-color: #f8f8f8;
  width: 155px;
  height: 35px;
  padding-left: 20px;
  background-image: url("../images/icons/arrowBottom.png");
  background-repeat: no-repeat;
  background-position: right 25px center;
  padding-right: 10px;
  box-sizing: border-box;
  cursor: pointer;
  padding-top: 10px;
}

.content .title {
  font-family: 'Source Sans Pro' , sans-serif;
  font-size: 30px;
  color: #000;
  font-weight: 400;
}

.sliderMint {
  width: 425px;
  float: left;
  margin: 0 65px;
}

.sliderMint .percentBreak {
  padding: 5px;
  min-width: 65px;
  background-color: #b01b52;
  position: absolute;
  top: 15px;
  right: 0;
  font-family: 'Roboto' , sans-serif;
  font-size: 22px;
  color: #fff;
  font-weight: 300;
  text-align: center;
}

.sliderMint .holste {
  width: 100%;
  position: relative;
}

.sliderMint .holste .navMint {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: -15px;
}

.sliderMint .holste img {
  width: 100%;
}

.sliderMint .bigImg {
  cursor: zoom-in;
}

.sliderMint[data-status="zoom"] .navMini {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
}

.sliderMint[data-status="zoom"] .navMini .link img {
  display: none;
}

.sliderMint[data-status="zoom"] .navMini .psevdoImg img {
  width: auto;
  height: 80vh;
  max-width: 100%;
}

.sliderMint[data-status="zoom"] .navMini .wrap {
  background-color: #fff;
  max-width: 90vw;
  box-sizing: border-box;
  padding: 30px 1vw;
  margin: 5vh auto;
  position: relative;
}

.sliderMint[data-status="zoom"] .navMini .anyHolste {
  width: 100%;
  float: left;
}

.sliderMint[data-status="zoom"] .navMini .navThe {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

.sliderMint[data-status="zoom"] .navMini .navThe .prev, .sliderMint[data-status="zoom"] .navMini .navThe .next {
  width: 30px;
  cursor: pointer;
}

.sliderMint[data-status="zoom"] .navMini .navThe .prev img, .sliderMint[data-status="zoom"] .navMini .navThe .next img {
  max-width: 100%;
}

.sliderMint[data-status="zoom"] .navMini .navThe .prev {
  position: absolute;
  left: 10px;
}

.sliderMint[data-status="zoom"] .navMini .navThe .next {
  position: absolute;
  right: 10px;
}

.sliderMint[data-status="zoom"] .navMini .close {
  width: 30px;
  height: 30px;
  position: absolute;
  top: -15px;
  right: -15px;
  background-color: #fff;
  border: 1px solid #999;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.sliderMint[data-status="zoom"] .navMini .close:hover {
  opacity: .95;
}

.sliderMint[data-status="zoom"] .navMini .close img {
  max-width: 80%;
}

.sliderMint[data-status="just"] .psevdoImg img {
  display: none;
}

.sliderMint[data-status="just"] .close {
  display: none;
}

.sliderMint[data-status="just"] .navThe {
  display: none;
}

.sliderMint .navMini {
  text-align: center;
}

.sliderMint .navMini .wrap {
  display: inline-block;
}

.sliderMint .navMini img {
  width: 90px;
}

.sliderMint .navMini .link {
  float: left;
  margin: 5px;
  box-sizing: border-box;
  cursor: pointer;
}

.sliderMint .navMini .link[data-status="active"] {
  border: 2px solid #b62b5e;
}

.sliderMint .arrow-prev {
  left: -30px;
  background-image: url("../images/icons/popularLeft.png");
  position: absolute;
  height: 35px;
  width: 20px;
  background-repeat: no-repeat;
  background-size: container;
  cursor: pointer;
}

.sliderMint .arrow-next {
  right: -30px;
  background-image: url("../images/icons/popularRight.png");
  position: absolute;
  height: 35px;
  width: 20px;
  background-repeat: no-repeat;
  background-size: container;
  cursor: pointer;
}

.breakPrice {
