				padding: 30px 1vw;
				margin: 5vh auto;
				position: relative; }
			.anyHolste {
				width: 100%;
				float: left; }
			.navThe {
				width: 100%;
				position: absolute;
				top: 50%;
				left: 0;
				.prev , .next {
					width: 30px;
					cursor: pointer;
					img {
						max-width: 100%; } }
				.prev {
					position: absolute;
					left: 10px; }
				.next {
					position: absolute;
					right: 10px; } }
			.close {
				width: 30px;
				height: 30px;
				position: absolute;
				top: -15px;
				right: -15px;
				background-color: #fff;
				border: 1px solid #999;
				display: flex;
				align-items: center;
				justify-content: center;
				cursor: pointer;
				&:hover {
					opacity: .95; }
				img {
					max-width: 80%; } } } }
	&[data-status="just"] {
		.psevdoImg {
				img {
					display: none; } }
		.close {
			display: none; }
		.navThe {
			display: none; } }
	.navMini {
